import { ToggleButtonGroup, alpha, styled } from "@rp/react/web/mui/core"

export const MonochromeToggleButtonGroup = styled(ToggleButtonGroup, {
  shouldForwardProp: prop => prop !== "size",
})(({ size, theme }) => ({
  "&.MuiToggleButtonGroup-root button.MuiToggleButtonGroup-grouped": {
    backgroundColor: "transparent",
    border: `1px solid ${alpha("#ffffff", 0.32)}`,
    borderLeft: "none",
    fontSize: size === "small" ? "14px" : "16px",
    height: size === "small" ? "32px" : "36.5px",
    padding: "6px 6px",
    color: theme.palette.textColor[700],
    "&:hover": {
      backgroundColor: alpha("#ffffff", 0.08),
    },
    "&.Mui-selected, &.Mui-selected:hover": {
      color: theme.palette.textColor[200],
      fontWeight: 700,
      backgroundColor: alpha("#ffffff", 0.12),
    },
    "&:first-of-type": {
      borderLeft: `1px solid ${alpha("#ffffff", 0.32)}`,
    },
  },
}))
